export default {
  name: 'suba-pagination',
  props: {
    now: {
      type: Number,
      default: 1
    },
    count: {
      type: Number,
      default: 0
    }
  },
  methods: {
    changePage: function (page) {
      if (this.now !== page) {
        if (page <= 0) {
          page = 1
        } else if (page >= this.count) {
          page = this.count
        }
        this.now = page
      }
    }
  },
  watch: {
    now (val, oldVal) {
      this.$emit('updatePageNow', val)
    }
  }
}



